<template>
  <component
    :is="tag"
    class="dropdown"
    :class="{show:isOpen}"
    @click="toggleDropDown"
    v-click-outside="closeDropDown"
  >
    <a class="nav-drop" :class="titleClasses" data-toggle="dropdown">
      <slot name="title">
        <img v-if="!type" src="@/assets/havr-icon/filterIcon.svg">
        <span :class="{'margin-filter-table': customTitleFilter}">
          {{title}}
          <img
            :class="{'rotate-custom-img' :isOpen }"
            class="dropdown-custom"
            src="@/assets/havr-icon/dropdown-menu-icon.svg"
            v-if="type"
          >
        </span>
      </slot>
    </a>
    <ul class="dropdown-menu custom-drop-down" :class="{show:isOpen}">
      <slot></slot>
    </ul>
  </component>
</template>
<script>
export default {
  props: {
    tag: {
      type: String,
      default: "li"
    },
    type: String,
    title: String,
    customTitleFilter: Boolean,
    icon: String,
    titleClasses: [String, Object, Array]
  },
  data() {
    return {
      isOpen: false
    };
  },
  methods: {
    toggleDropDown() {
      this.isOpen = !this.isOpen;
      this.$emit("change", this.isOpen);
    },
    closeDropDown() {
      this.isOpen = false;
      this.$emit("change", false);
    }
  }
};
</script>


<style>
.nav-drop {
  position: initial;
}

.dropdown-custom {
  width: 8%;
  margin-left: 10px;
}

.rotate-custom-img {
  transform: rotate(180deg);
  margin-bottom: 2px;
}

.custom-drop-down {
  position: relative !important;
  left: -53px !important;
  background: white !important;
  top: 20px !important;
}

.margin-filter-table {
  margin-left: 8px;
  font-size: 14px;
  height: 16px;
  position: relative;
  letter-spacing: -0.2px;
  color: #b3b3b3;
  left: 4px;
}
</style>
