<template>
  <footer class="footer fixed">
    <notifications class="alert-info" group="foo"/>
    <div class="container-fluid d-flex flex-wrap justify-content-between vertical-centering">
      <nav>
        <ul>
          <li>
            <input
              type="button"
              value="20"
              :disabled="store.state.user.rowKeyChain < 20"
              :class="{'activeButton': store.state.user.selectedItemByPage === 20 }"
              @click="setPaginationTable(20)"
              class="item-table-selector pointer selector-last-page center-selector left-selector"
            >
            <input
              type="button"
              value="50"
              :disabled="store.state.user.rowKeyChain < 50"
              :class="{'activeButton': store.state.user.selectedItemByPage === 50 }"
              @click="setPaginationTable(50)"
              class="item-table-selector pointer center-selector"
            >
            <input
              type="button"
              value="100"
              :disabled="store.state.user.rowKeyChain < 100"
              :class="{'activeButton': store.state.user.selectedItemByPage === 100 }"
              @click="setPaginationTable(100)"
              class="item-table-selector pointer selector-next-page center-selector right-selector next-page"
            >
            <span
              style="margin-left: 16px;"
              class="paragraph-small-regular-secondary"
            >users per page</span>
          </li>
          <li class="fixed-bottom-right">
            {{store.state.user.startPaginationItem}} - {{store.state.user.endPaginationItem}} of {{filterRow}}
            <button
              :disabled="store.state.user.startPaginationItem === 1"
              @click="move('backward')"
              class="item-table-selector arrow pointer selector-last-page"
            >
              <i class="ti-angle-left grey"></i>
            </button>
            <button
              :disabled="store.state.user.endPaginationItem === filterRow"
              @click="move('forward')"
              class="item-table-selector arrow pointer selector-next-page"
            >
              <i class="ti-angle-right grey"></i>
            </button>
          </li>
        </ul>
      </nav>
      <div class="copyright d-flex flex-wrap"></div>
    </div>
  </footer>
</template>
<script>
import store from "@/flulux/store";
export default {
  data() {
    return {
      store: store
    };
  },
  mounted() {
    this.setPaginationTable(20);
  },
  computed: {
    filterRow() {
      if (store.state.user.rowKeyChain > 20)
        return store.state.user.rowKeyChain;
      else return store.state.user.endPaginationItem;
    }
  },
  methods: {
    /**
     * map pagination to data store
     */
    setPaginationTable(p) {
      if (store.state.user.rowKeyChain < p) {
        this.store.actions.SET_END_PAGINATION_USERS(
          store.state.user.rowKeyChain
        );
      } else {
        this.store.actions.SET_PAGINATION_USERS_TABLE(p);
        this.store.actions.SET_END_PAGINATION_USERS(p);
      }
    },
    move(state) {
      if (state === "forward") {
        if (
          store.state.user.selectedItemByPage +
            store.state.user.startPaginationItem >
            store.state.user.rowKeyChain ||
          store.state.user.selectedItemByPage +
            store.state.user.endPaginationItem +
            1 >
            store.state.user.rowKeyChain
        )
          return this.setPaginationTable(store.state.user.rowKeyChain + 1);
        this.store.actions.SET_START_PAGINATION_USERS(
          store.state.user.selectedItemByPage +
            store.state.user.startPaginationItem
        );
        this.store.actions.SET_END_PAGINATION_USERS(
          store.state.user.selectedItemByPage +
            store.state.user.endPaginationItem +
            1
        );
      } else {
        if (
          store.state.user.selectedItemByPage -
            store.state.user.startPaginationItem >
            store.state.user.startPaginationItem ||
          store.state.user.selectedItemByPage -
            store.state.user.endPaginationItem >
            store.state.user.endPaginationItem
        )
          return this.setPaginationTable(store.state.user.rowKeyChain + 1);
        this.store.actions.SET_START_PAGINATION_USERS(
          store.state.user.startPaginationItem -
            store.state.user.selectedItemByPage
        );
        this.store.actions.SET_END_PAGINATION_USERS(
          store.state.user.endPaginationItem -
            1 -
            store.state.user.selectedItemByPage
        );
      }
    }
  }
};
</script>
