 <template>
  <form
    method="post"
    action="https://preprodapi.havr.io/oauth/v1/simple_login_form"
    enctype="multipart/form-data"
  >
    name:
    <input type="mail" name="email_or_username" value="x@y.com">
    <br>password :
    <input type="password" name="password" value="pass">
    <br>
    <input type="hidden" name="client_id" value="web_plateform_client_id">
    <input type="hidden" name="grant_type" value="password">
    <input type="hidden" name="user_agent" value="unknown">
    <input type="submit" value="Submit">
  </form>
</template>
 <script>
import userRepository from "@/repository/users";
export default {
  data() {
    return {};
  },
  mounted() {
    if (this.$route.query.success === "false") {
      return this.$notify({
        message: "Invalid credentials",
        type: "danger",
        timeout: 2000,
        horizontalAlign: "right",
        verticalAlign: "top"
      });
    }
    this.checkConnection();
  },
  methods: {
    async login() {
      sessionStorage.setItem("token", this.$route.query.access_token);
      sessionStorage.setItem("refresh_token", this.$route.query.refresh_token);
      const resp = await userRepository.retrieveUser(
        this.$route.query.user_id,
        true
      );
      const img = await userRepository.retrieveContactPicture(resp.id);
      if (img.base64) {
        resp.profile_picture = img.base64;
      }
      sessionStorage.setItem("currentUser", JSON.stringify(resp));
      this.$router.push("loading-data");
    },
    async checkConnection() {
      if (this.$route.query.access_token) {
        return await this.login();
      } else return;
    }
  }
};
</script>

